* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

:root {
  --light: #f3f1f0;
  --dark: #121212;
}

body {
  font-family: "Poppins", sans-serif;
  color: var(--dark);
  background-color: var(--light);
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
}
